import { getCurrentUser } from '../services/UsersAPI';
import { getCookie } from '../lib/Session';

export default class Account extends React.Component {
	static async getIntitialProps(ctx) {
		const jwt = getCookie('jwt', ctx);
		if (jwt) {
			const user = await getCurrentUser(jwt);
		}
	}

	render() {
		return <h1>My Account</h1>;
	}
}
